.timeline {
  line-height: 1.4em;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%
}

.timeline h1,
.timeline h2,
.timeline h3,
.timeline h4,
.timeline h5,
.timeline h6 {
  line-height: inherit
}

.timeline-item {
  padding-left: 40px;
  position: relative
}

.timeline-item:last-child {
  padding-bottom: 0
}

.timeline-info {
  letter-spacing: 3px;
  margin: 0 0 .5em 0;
  white-space: nowrap
}

.timeline-marker {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 15px
}

.timeline-marker:before {
  background: #d1d5da;
  border: 3px solid transparent;
  border-radius: 100%;
  content: "";
  display: block;
  height: 15px;
  position: absolute;
  top: 4px;
  left: 0;
  width: 15px;
  transition: background 0.3s ease-in-out, border 0.3s ease-in-out
}

.timeline-marker:after {
  content: "";
  width: 3px;
  background: #e1e4e8;
  display: block;
  position: absolute;
  top: 25px;
  bottom: 0;
  left: 9px
}

.timeline-item:last-child .timeline-marker:after {
  content: none
}

.timeline-item:not(.period):hover .timeline-marker:before {
  background: transparent;
  border: 3px solid #79b8ff
}

.timeline-content {
  padding-bottom: 40px
}

.timeline-content p:last-child {
  margin-bottom: 0
}

@media (min-width: 768px) {
  .timeline-split .timeline {
    display: table
  }

  .timeline-split .timeline-item {
    display: table-row;
    padding: 0
  }

  .timeline-split .timeline-info,
  .timeline-split .timeline-marker,
  .timeline-split .timeline-content,
  .timeline-split .period .timeline-info {
    display: table-cell;
    vertical-align: top
  }

  .timeline-split .timeline-marker {
    position: relative
  }

  .timeline-split .timeline-content {
    padding-left: 30px
  }

  .timeline-split .timeline-info {
    padding-right: 30px
  }

  .timeline-split .period .timeline-title {
    position: relative;
    left: -45px
  }
}

@media (min-width: 544px) {

  .timeline-centered,
  .timeline-centered .timeline-item,
  .timeline-centered .timeline-info,
  .timeline-centered .timeline-marker,
  .timeline-centered .timeline-content {
    display: block;
    margin: 0;
    padding: 0
  }

  .timeline-centered .timeline-item {
    padding-bottom: 40px;
    overflow: hidden
  }

  .timeline-centered .timeline-marker {
    position: absolute;
    left: 50%;
    margin-left: -7.5px
  }

  .timeline-centered .timeline-info,
  .timeline-centered .timeline-content {
    width: 50%
  }

  .timeline-centered>.timeline-item .timeline-info {
    float: left;
    text-align: right;
    padding-right: 30px
  }

  .timeline-centered>.timeline-item .timeline-content {
    float: right;
    text-align: left;
    padding-left: 30px
  }

  .timeline-centered>.timeline-item.period .timeline-content {
    float: none;
    padding: 0;
    width: 100%;
    text-align: center
  }

  .timeline-centered .timeline-item.period {
    padding: 50px 0 90px
  }

  .timeline-centered .period .timeline-marker:after {
    height: 30px;
    bottom: 0;
    top: auto
  }

  .timeline-centered .period .timeline-title {
    left: auto
  }
}

